
<template>
  <div class="hot">
      <HomeNav></HomeNav>
      <div class="hot-box">
          <div class="hot-img"></div>
          <div class="time">
            更新时间 05月07日
          </div>
      </div>
      <div class="hotItem">
          <HotList v-for="(item, index) in hotSong" :key="index" :hotItem="item" :options="{order: index}"  @translate-song="$emit('translate-song', $event)" :currentSong="currentSong"></HotList>

      </div>
  </div>
</template>
<script>

import HomeNav from '@/components/HomeNav.vue'
import HotList from '@/components/HotList.vue'

  export default {
        name: 'Hot',

        props: {
            currentSong: Object
        },

        components: {
            HomeNav,
            HotList
        },

        data() {
            return {
              hotSong: []
            };
        },

        methods: {
            getHomSong() {

                // this.axios.get('https://music.kele8.cn/personalized')
                // .then((data) => {
                //     console.log('data ==>', data)
                // })
                // .catch((err) => {
                //   console.log('err ==>', err);
                // })

                this.axios({
                    method: 'GET',
                    url: 'https://music.kele8.cn/top/list?idx=2'
                    // url: 'https://music.kele8.cn/personalized/newsong'
                }).then(response => {
                    // 如果数据正确
                    // console.log(response);
                    
                    this.hotSong = response.data.playlist.tracks;
                    console.log('this.hotSong ==>', this.hotSong);

                    // 将数据放进本地存储
                    window.localStorage.setItem('hotSong', JSON.stringify({
                    // 过期时间
                      expire: Date.now() + 2 * 60 * 60 * 1000,
                      result: response.data.playlist.tracks
                    }))
                    
                }).catch(err => {
                    console.log('err ==>', err);
                })

            },

        },

        created() {

            // this.getHomSong();

            const localHotSongData = JSON.parse(window.localStorage.getItem('hotSong'));

            if (localHotSongData && localHotSongData.expire > Date.now()) {
              // 存在且还没有过期
              this.hotSong = localHotSongData.result;

            } else {
              // 已经过期
              this.getHomSong();

            }

        }
  }



</script>

