    .hot-box {
        background-color: #ab5d47;
        position: relative;
        width: 100%;
        height: 175px;

        .hot-img {
            position: absolute;
            top: 38px;
            left: 30px;
            width: 142px;
            height: 67px;
            background: url('../assets/images/index_icon_2x.png') no-repeat;
            background-position: -24px -30px;
            background-size: 166px 97px;
        }

        .time {
            position: absolute;
            top: 120px;
            left: 30px;
            color: #fff;
            font-size: 12px;
        }
    }
</style>